import React from 'react';
import "./styles/ourApp.css";
import app from '../img/app.svg';
import programing from '../img/hacker.svg';
export default function OurApp() {
    return (
        <section className="App">
            <h1 className="App__title">Descarga nuestra aplicacion para el seguimiento de tus pagos</h1>
            <img src={app} className="App__img"></img>
            <p className="App__sms">Upps!! nuestra app aun se encuentra en desarrollo ...</p>
            <img src={programing} className="App__img-dev"></img>
        </section>
    )
}
