import React, { Component } from 'react';
import {Link, link} from 'react-router-dom';
import menu from '../img/menu.svg';
import logo from '../img/logo.svg';
import iconX from '../img/letra-x.svg';
import "./styles/header.css";
import './styles/nav.css';
export default class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            classNav:"",
            menuNav:menu
        }
        this.handleClickNav=this.handleClickNav.bind(this);
    }
    handleClickNav(){
        if (this.state.classNav=="") {
            this.setState({classNav:"Block", menuNav:iconX });

        }else{
            this.setState({classNav:"",menuNav:menu });
        }
    }
    render() {
        return (
            <React.Fragment>
                <nav className={"Nav "+this.state.classNav}>
                    <header className="Header">
                        <img className="Header__logo" src={logo}></img>
                        <img className="Header__menu" onClick={this.handleClickNav} src={this.state.menuNav} alt="menu"></img>
                    </header>
                    <ul className="Nav__list">
                        <li className="Nav__Container_element"><Link onClick={this.handleClickNav} to='/home' className="Nav__list_element">  Home          </Link></li>
                        <li className="Nav__Container_element"><Link onClick={this.handleClickNav} to='/paquetes' className="Nav__list_element">       Paquetes      </Link></li>
                        <li className="Nav__Container_element"><Link onClick={this.handleClickNav} to='/contactanos' className="Nav__list_element">       Contactanos       </Link></li>
                        <li className="Nav__Container_element"><Link onClick={this.handleClickNav} to='/app' className="Nav__list_element">       Descarga la app</Link></li>
                    </ul>
                </nav>
            </React.Fragment>
        )
    }
}