import React from 'react';
import whatsapp from '../img/whatsapp.svg';
import facebook from '../img/facebook.svg';
import instagram from '../img/instagram.svg';
import './styles/contact.css';
export default function Contact() {
    return (
        <section className="Contact">
            <h1 className="Contact__title">Contactanos en Facabook, whatsapp o instagram.</h1>
            <div className="Contact__container">
                <div className="Element">
                    <p className="Contact__paragraph" >998 108 8134</p>
                    <img src={whatsapp} className="Contact__img"></img>
                </div>
                <div className="Element">
                    <p className="Contact__paragraph" >@Miniviajesmx</p>
                    <img src={facebook} className="Contact__img"></img>
                </div>
                <div className="Element">
                    <p className="Contact__paragraph" >@Miniviajesmx</p>
                    <img src={instagram} className="Contact__img"></img>
                </div>
                
            </div>
        </section>
    )
}
