import React, { Component } from 'react';
import './styles/home.css';
import{Link} from 'react-router-dom';
import telefono from '../img/telefono.svg';
import trip from '../img/trip.svg';
import planes from '../img/planes.svg';
import app from '../img/app.svg';
import Footer from '../components/Footer.js';
import metodo_pago from '../img/metodo-de-pago.svg';
export default class Home extends Component {
    render() {
        return (
            <section className="Home">
                <h3 className="Home__title">Mini<b className="Home__title_aditional">ViajesMX</b></h3>
                <div className="Home__header_container">
                    <p className="Home__aside">Viajes desde $35 MXN </p>
                </div>
                <p className="Home__subtitle">¿Cómo puedo viajar con 35 pesos?</p>
                <ul className="Home__list">
                    <li className="List__element">
                        <p>Escoje uno de nuestros paquetes o arma el tuyo.</p>
                        <Link to="/paquetes" className="Element__link">Conoce nuestros paquetes</Link> 
                        <img src={planes} className="Home__img"></img>
                    </li> 
                    <li className="List__element">
                        <p>Al elegir un paquete tambien te pones en contacto para personalizarlo o aclarar dudas.</p> 
                        <Link to="/contactanos" className="Element__link">Contactanos</Link>
                        <img src={telefono} className="Home__img"></img>
                    </li>
                    <li className="List__element">
                        <p>Descarga nuestra aplicacion para seguir tus pagos realizados.</p>
                        <Link to="/app" className="Element__link">Descarga la app</Link>
                        <img src={app} className="Home__img"></img>
                    </li>
                    <li className="List__element">
                        <p>Realiza tus pagos de manera puntual.</p>
                        <img src={metodo_pago} className="Home__img"></img>
                    </li>
                    <li className="List__element">
                        <p>Disfruta tu viaje.</p>
                        <img src={trip} className="Home__img"></img>                            
                    </li>
                </ul>
                <Footer />
            </section>
        )
    }
}
